.hero-bgd {
  position: absolute;
  color: #ac46e3;
  top: 0;
  left: 0;
  right: 0;
  height: 500px;

  background: radial-gradient(#ac46e3 15%, transparent 16%), linear-gradient(45deg, transparent 49%, #ac46e3 49% 51%, transparent 51%), linear-gradient(-45deg, transparent 49%, #ac46e3 49% 51%, transparent 51%);
  background-size: 1em 1em;
  background-color: #ffffff;
  opacity: 0.2;
    
  z-index: 0;
}

.hero-content-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
    margin-top: 80px;
    padding-bottom: 30px;
    height: 30%;
    z-index: 999;
    font-family: 'Montserrat', sans-serif;
  }
  
  .hero .hero-image {
    /* flex: 1 1 50%; */
    text-align: center;
     z-index: 999;
  }
  
  .hero-image img {
    height: auto;
    max-width: 400px;
    max-height: 400px;
  }
  
  .hero-text {
    /* flex: 1 1 50%; */
    text-align: center;
    padding: 20px;
    color: #ac46e3;
    z-index: 10;
  }
  
  .hero-text h1 {
    font-size: 3rem;
    margin-bottom: 20px;
  }
  
  .hero-text p {
    font-size: 1.5rem;
    margin-bottom: 40px;
  }
  
  /* .hero-text button {
    padding: 10px 20px;
    font-size: 1.2rem;
    background-color: #008CBA;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  } */

  .hero-text .btn {
  border: 2px solid purple;
  outline: 0;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 4px;
  min-width: 170px;
  display: inline-block;
  color: purple;
