.gallery-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 30px;
  gap: 30px;
  margin-top: 50px;
}

@media screen and (max-width: 768px) {
  .gallery-section {
    grid-template-columns: 1fr;
  }
}

.gallery-item {
  position: relative;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  color: plum;
}

.link {
  text-decoration: none;

}

.gallery-item img {
  display: block;
  width: 100%;
  object-fit: cover;
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  height: 350px;
}

.gallery-item:hover img {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.gallery-item h3 {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.gallery-item:hover h3 {
  opacity: 1;
}

.more-details-btn {
  display: block;
  margin: 20px auto 0;
  padding: 10px 20px;
  background-color: #fff;
  color: #000;
  border: none;
  border-radius: 30px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: -webkit-transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out;
  transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}

.more-details-btn:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.blog {
  margin-top: 100px;
}


.blog__post {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
  padding-top: 50px;
  padding-bottom: 20px;
}

.blog__post img {
  height: 300px;
  width: 50%;
  object-fit: contain;
}

.blog__post-content {
  margin-left: 20px;
}

.blog__post h3 {
  font-size: 1.5rem;
  margin: 0;
}

.blog__post p {
  margin: 5px 0 10px 0;
}

.blog__post button {
  background-color: #6b5b95;
  color: #fff;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.product-details {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 150px;
}

.product-image {
  /* flex-basis: 50%; */
  /* width: 100%; */
  padding: 3em 0;
  background: #9b45d5;
}

.product-image img {
  width: 100%;
  height: 400px;
  object-fit: contain;
}

.product-info {
  flex-basis: 50%;
  margin-left: 30px;
}

.product-info h1 {
  font-size: 24px;
  margin-bottom: 20px;
}

.product-info p {
  margin-bottom: 20px;
  line-height: 1.5;
}

.price {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}

.add-to-cart-btn {
  display: block;
  padding: 10px;
  background-color: #9b45d5;
  color: #fff;
  border: none;
  border-radius: 3px;
  font-size: 16px;
  cursor: pointer;
}

.add-to-cart-btn:hover {
  background-color: #75558a;
}

